.field {
  width: 100%;
  min-height: 100%;
  display: grid;
  margin-bottom: 16px;
  font-family: "PTSans", "Arial", sans-serif;
}

.field__label {
  grid-row: 1/2;
  margin: 0 0 0 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
}

.field__input {
  grid-row: 2/3;
  max-height: 58px;
  padding: 16px 58px 16px 16px;
  margin: 0;
  background-color: #f2f2f2;
  color: #000000;
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  border: none;
  cursor: pointer;
}
.field__input:hover {
  background-color: #e5e5e5;
}
.field__input:active, .field__input:focus {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
}

@media (min-width: 768px) {
  .field__input {
    min-width: 280px;
    box-sizing: border-box;
  }
}
.field__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.radio {
  position: relative;
  padding: 0;
  margin: 10px 0 36px 0;
}

@media (min-width: 768px) {
  .radio {
    margin-bottom: 30px;
  }
}
@media (min-width: 1280px) {
  .radio {
    min-width: 280px;
    width: 280px;
    margin: 0;
  }
}
.radio__control-label {
  display: block;
  margin: 0 0 0 62px;
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.radio__control-mark {
  position: absolute;
  top: -10px;
  left: 0;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  border: none;
  background-color: #f2f2f2;
  cursor: pointer;
}

.radio__input[type=radio]:checked + .radio__control-mark::before {
  position: absolute;
  content: "";
  top: 32%;
  left: 33%;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #333333;
  background-position: center;
  background-repeat: no-repeat;
}

.radio__input[type=radio]:hover + .radio__control-mark {
  background-color: #e5e5e5;
}

.checkbox__input[type=checkbox]:active + .radio__control-mark::before {
  opacity: 30%;
}

.radio__input[type=radio]:active + .radio__control-mark {
  background-color: #d5d5d5;
}

.checkbox {
  position: relative;
  padding: 0;
  margin: 10px 0 36px 0;
}

@media (min-width: 768px) {
  .checkbox {
    margin-bottom: 30px;
  }
}
@media (min-width: 1280px) {
  .checkbox {
    margin: 0;
    width: 172px;
    min-width: 172px;
  }
}
.checkbox__control-label {
  display: block;
  margin: 0 0 0 62px;
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.checkbox__control-mark {
  position: absolute;
  top: -10px;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.checkbox__input[type=checkbox]:checked + .checkbox__control-mark::before {
  position: absolute;
  content: "";
  top: 30%;
  left: 28%;
  width: 22px;
  height: 16px;
  mask-image: url("../img/svg/stack.svg#tick");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: #000000;
}

.checkbox__input[type=checkbox]:hover + .checkbox__control-mark {
  background-color: #e5e5e5;
}

.checkbox__input[type=checkbox]:active + .checkbox__control-mark::before {
  opacity: 30%;
}

.checkbox__input[type=checkbox]:active + .checkbox__control-mark {
  background-color: #d5d5d5;
}

