.footer {
  width: 100%;
  min-width: 320px;
  min-height: 243px;
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .footer {
    width: 100%;
    display: flex;
    row-gap: 20px;
    min-width: 653px;
    min-height: 48px;
    padding: 36px 66px 36px 50px;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    box-sizing: border-box;
    margin: 0 auto;
  }

  .footer--catalog,
  .footer--form {
    padding: 36px 52px 36px 45px;
  }
}

@media (min-width: $desktop-width) {
  .footer {
    width: 1200px;
    min-height: 48px;
    padding: 36px 126px 36px 75px;
    justify-content: space-between;
  }

  .footer--catalog,
  .footer--form {
    padding: 36px 109px 36px 70px;
  }
}

.footer__logo {
  display: block;
  margin: 0 auto 26px auto;
  padding: 39px 77px 0 90px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .footer__logo {
    margin: 0 59px 0 0;
    padding: 0;
    flex: 1 1 auto;
  }
}

@media (min-width: $desktop-width) {
  .footer__logo {
    margin: 0 143px 0 0;
    padding: 0;
    flex: 1 1 auto;
  }
}

.footer__socials {
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0 77px;
}

@media (min-width: $tablet-width) {
  .footer__socials {
    margin: 0 70px 0 0;
    padding: 0;
    flex: 1 1 auto;
  }
}

.footer__copyright {
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0 77px;
}

@media (min-width: $desktop-width) {
  .footer__socials {
    margin: 0;
  }
}

@media (min-width: $tablet-width) {
  .footer__copyright {
    margin: 0;
    padding: 0;
    flex: 1 1 auto;
  }
}

@media (min-width: $desktop-width) {
  .footer__copyright {
    width: 254px;
  }
}
