.banner2 {
  display: block;
  position: relative;
  min-width: 320px;
  min-height: 158px;
  margin: 0 auto;
  padding: 0;
  background-image: url("../img/backgroundcatalog-mobile.jpg");
  background-size: 902px 214px;
  background-repeat: no-repeat;
  background-position: 50% 100%;

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/svg/stack.svg#banner-corner");
    background-size: 1201px 57px;
    background-repeat: no-repeat;
    background-position: center;
    top: 101px;
    left: 0;
    right: 0;
    min-height: 57px;
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/backgroundcatalog-mobile@2x.jpg");
  }
}

.banner2__picture-text {
  display: block;
  padding-top: 52px;
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .banner2 {
    min-width: 768px;
    min-height: 290px;
    background-image: url("../img/backgroundcatalog-tablet.jpg");
    background-size: 1200px 518px;

    &::after {
      top: 233px;
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/backgroundcatalog-tablet@2x.jpg");
    }
  }
}

@media (min-width: $desktop-width) {
  .banner2 {
    min-height: 301px;

    &::after {
      top: 244.5px;
    }
  }
}

@media (min-width: $tablet-width) {
  .banner2__picture-text {
    padding-top: 95px;
    width: 457px;
    height: 78px;
  }
}

@media (min-width: $desktop-width) {
  .banner2__picture-text {
    padding-top: 103.5px;
    width: 460px;
    height: 78px;
  }
}

@media (min-width: $desktop-width) {
  .banner2__picture-text--fotm {
    padding-top: 101px;
    height: 80px;
  }
}
