.advantages {
  min-width: 320px;
  font-family: "PTSans", "Arial", sans-serif;
}

@media (min-width: $tablet-width) {
  .advantages {
    min-width: 768px;
  }
}

.advantages__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PTSans", "Arial", sans-serif;
  font-style: normal;
  text-transform: uppercase;
  list-style-type: none;
  counter-reset: counter-name;
}

@media (min-width: $tablet-width) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto auto auto auto;
    min-width: 768px;
  }

  .advantages__grid1 {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .advantages__grid2 {
    grid-column: 2/4;
    grid-row: 1/2;
  }

  .advantages__grid3 {
    grid-column: 1/2;
    grid-row: 2/3;
  }

  .advantages__grid4 {
    grid-column: 2/3;
    grid-row: 2/3;
  }

  .advantages__grid5 {
    grid-column: 3/4;
    grid-row: 2/3;
  }

  .advantages__grid6 {
    grid-column: 3/4;
    grid-row: 3/4;
  }

  .advantages__grid7 {
    grid-column: 1/3;
    grid-row: 3/4;
  }

  .advantages__grid8 {
    grid-column: 1/2;
    grid-row: 4/5;
  }

  .advantages__grid9 {
    grid-column: 2/3;
    grid-row: 4/5;
  }

  .advantages__grid10 {
    grid-column: 3/4;
    grid-row: 4/5;
  }
}

.advantages__design1 {
  padding: 69px 18px 18px 18px;
  margin: 0;
  background-color: #81b3d2;
  color: #ffffff;
  text-align: center;
}

.advantages__design1--intends {
  padding-top: 37px;
}

@media (min-width: $tablet-width) {
  .advantages__design1 {
    min-height: 256px;
    min-width: 256px;
    box-sizing: border-box;
    padding: 52px 33px 7px 33px;
  }
}

@media (min-width: $desktop-width) {
  .advantages__design1 {
    min-height: 256px;
    min-width: 300px;
    box-sizing: border-box;
    padding: 52px 50px 30px 50px;
  }
}

.advantages__title1 {
  display: inline-block;
  min-width: 284px;
  margin: 0 auto 11px auto;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.advantages__description1 {
  margin: 0 auto 20px auto;
  display: block;
  min-width: 284px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  word-wrap: break-word;
}

@media (min-width: $tablet-width) {
  .advantages__title1 {
    min-width: 190px;
    margin: 0 auto 0 auto;
    font-size: 21px;
    line-height: 26px;
  }

  .advantages__description1 {
    min-width: 190px;
    margin-bottom: 7px;
  }
}

@media (min-width: $desktop-width) {
  .advantages__title1 {
    font-size: 19px;
    line-height: 26px;
  }

  .advantages__description1 {
    font-size: 15px;
    line-height: 21px;
  }
}

.advantages__design2 {
  min-width: 320px;
  min-height: 260px;
  width: 100%;
}

.advantages__picture {
  display: block;
  object-fit: cover;
  width: 100%;
}

@media (min-width: $tablet-width) {
  .advantages__design2 {
    min-width: 512px;
    min-height: 256px;
    width: 100%;
  }

  .advantages__picture {
    height: 100%;
    min-height: 256px;
  }
}

.advantages__design3 {
