.advantages__title3--burger::before {
  position: absolute;
  content: "";
  background-image: url("../img/svg/stack.svg#burger");
  background-size: 40px 35px;
  background-repeat: no-repeat;
  background-position: center;
  width: 40px;
  height: 35px;
  top: 0;
  left: -47px;
}

@media (min-width: $tablet-width) {
  .advantages__title3--burger::before {
    display: none;
  }

  .advantages__grid4::before {
    content: "";
    grid-row: 1/2;
    background-image: url("../img/svg/stack.svg#burger");
    background-size: 80px 70px;
    background-repeat: no-repeat;
    background-position: center;
    width: 80px;
    height: 70px;
    margin: 0 auto;
  }
}

@media (min-width: $desktop-width) {
  .advantages__grid4::before {
    margin-bottom: 25.1px;
    background-size: 73px 69px;
    width: 73px;
    height: 69px;
  }
}

